.wpp-thakyou-page > .order_details {
  padding: 20px 30px;
}

.wpp-thakyou-page > .order_details > div {
  margin: 20px 0;
}

.wpp-thakyou-page > .order_details > div span {
  font-size: 18px;
  font-weight: 600;
}

.wpp-thakyou-page > .order_details > hr {
  margin: 30px 0;
}

.wpp-thakyou-page > .order_details > .billet-url > div {
  margin: 10px 0;
}

.wpp-thakyou-page > .order_details > .billet-url > div a {
  color: #fff;
  background-color: #65a300;
  border-radius: 5px;
  padding: 10px 20px;
  text-decoration: none;
}

.wpp-thakyou-page > .order_details > .billet-url > div a:hover {
  opacity: .9;
}

.wpp-thakyou-page > .order_details > .billet-content {
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.wpp-thakyou-page > .order_details > .billet-content > .billet-line > div > span {
  background-color: #65a300;
  border-radius: 5px;
  margin-left: 5px;
  padding: 10px;
}

.wpp-thakyou-page > .order_details > .billet-content > .billet-line > div > span i {
  color: #fff;
}

.wpp-thakyou-page > .order_details > .billet-content > .billet-line > div > span:hover {
  cursor: pointer;
  opacity: .9;
}

